<template>
  <div class="mine-page flex column js">
    <div class="item">
      <div class="mine box flex ac">
        <CustomImg class="portait" width="1.33rem" height="1.33rem" :src="list.head_pic" round></CustomImg>
        <div>
          <div class="nickname flex ac">
            {{list.nickname | longtext}}
            <img :src="require('@/assets/me.png')" />
          </div>
          <div class="nums">{{list.mobile}}</div>
        </div>
      </div>
      <div class="bind">
        <div class="box wx flex ac js" @click="wxAuth(list.openid)">
          <div>关联微信</div>
          <div class="flex center">{{list.openid ? '已绑定' : '未绑定'}}</div>
        </div>
        <span v-if='!isBind' class="bind-tip">点击立即绑定</span>
      </div>
    </div>
    <div class="btn box">
      <van-button type="default" block color="#E84114" size="large" @click="logOut">退出登录</van-button>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Button } from "vant";
import { getUserInfo } from "@/api";
import { getWxCodeToAccess, bindWx } from "@/api";
import { wxAuth } from "@/utils/wx";

Vue.use(Button);

export default {
  data() {
    return {
      list: {},
      isBind: true
    };
  },
  created() {
    const { code } = this.$route.query;
    this.getUserInfo();
    if (code) {
      this.wxLogin();
    }
  },
  methods: {
    // 退出
    logOut() {
      localStorage.clear();
      this.$router.replace("/login");
    },
    // 绑定微信
    wxAuth(openid) {
      if (!openid) {
        const url = window.btoa(location.origin + this.$route.path);
        wxAuth(url);
      }
    },
    // 获取用户信息
    getUserInfo() {
      getUserInfo({ login_platform: "h5" }).then(res => {
        if (res.success) {
          this.list = res.data;
          this.isBind = !!res.data.openid
          const data = JSON.stringify(res.data);
          localStorage.setItem("userInfo", data);
          localStorage.setItem("token", res.data.token);
          localStorage.setItem("user_id", res.data.user_id);
        }
      });
    },
    async wxLogin() {
      const { code } = this.$route.query;
      if (code) {
        const result = await getWxCodeToAccess({ code });
        if (result.success) {
          const { openid, unionid } = result.data;
          const result2 = await bindWx({ openid, unionid });
          if (result2.success) {
            this.$toast('绑定成功')
            this.getUserInfo()
          }
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
.mine-page {
  box-sizing: border-box;
  height: 100%;
  padding-bottom: 72px;
  padding-bottom: calc(72px + constant(safe-area-inset-bottom));
  padding-bottom: calc(72px + env(safe-area-inset-bottom));
}

.box {
  background: rgba(255, 255, 255, 1);
  border-radius: 8px;
  margin: 10px 13px;
}

.mine {
  background: #fff;
  border-radius: 8px;
}

.portait {
  margin: 15px 10px 15px 13px;
}

.nickname {
  color: #282828;
  font-size: 14px;
  line-height: 20px;
  img {
    width: 14px;
    margin-left: 5px;
  }
}

.nums {
  color: #777;
  font-size: 12px;
  margin-top: 5px;
  line-height: 17px;
}

.wx {
  height: 49px;
  background: rgba(255, 255, 255, 1);
  border-radius: 8px;
  padding: 0 10px;
  font-size: 14px;
  color: #282828;
  div:first-child {
    color: #666;
  }
}

.bind{
  position: relative;
}

.bind-tip{
  display: inline-block;
  height: 15px;
  line-height: 15px;
  width: 70px;
  font-size: 9px;
  color: #7A492F;
  text-align: center;
  background:rgba(255,237,161,1);
  box-shadow:0px 2px 4px 0px rgba(193,193,193,0.5);
  border-radius: 14px;
  position: absolute;
  top: -5px;
  right: 5px;
  z-index: 1;
  &::after{
    content: ' ';
    width: 0;
    height:0;
    border-left: 3px solid transparent;
    border-top: 3px solid transparent;
    border-right: 3px solid rgb(255,237,161);
    border-bottom: 3px solid rgb(255,237,161);
    // box-shadow:0px 2px 4px 0px rgba(193,193,193,0.5);
    position: absolute;
    bottom: -3px;
    left: 50%;
    transform: translateX(-50%) rotate(45deg)
  }
}
</style>